ь\n@mixin hidden {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 1px;\n  height: 1px;\n  clip: rect(1px, 1px, 1px, 1px);\n}\n\n// ------------------\n\n// Заголовки\n\n@mixin h {\n  font-style: normal;\n  font-weight: 700;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n}\n\n@mixin h1 {\n  @include h;\n  font-size: 36px;\n  line-height: 48px;\n}\n\n@mixin h2 {\n  @include h;\n  font-size: 32px;\n  line-height: 40px;\n}\n\n@mixin h3 {\n  @include h;\n  font-size: 30px;\n  line-height: 40px;\n}\n\n@mixin h4 {\n  @include h;\n  font-size: 26px;\n  line-height: 32px;\n}\n\n@mixin h5 {\n  @include h;\n  font-size: 22px;\n  line-height: 32px;\n}\n\n@mixin h6 {\n  @include h;\n  font-size: 18px;\n  line-height: 24px;\n}\n\n// Подзаголовки\n\n@mixin s {\n  font-style: normal;\n  font-weight: 500;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n  line-height: 24px;\n}\n\n@mixin s1 {\n  @include s;\n  font-size: 15px;\n}\n\n@mixin s2 {\n  @include s;\n  font-size: 13px;\n}\n\n// Тексты\n\n@mixin t {\n  font-style: normal;\n  font-weight: 400;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n}\n\n@mixin p1 {\n  @include t;\n  font-size: 15px;\n  line-height: 20px;\n}\n\n@mixin p2 {\n  @include t;\n  font-size: 13px;\n  line-height: 18px;\n}\n\n@mixin c1 {\n  @include t;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin c2 {\n  @include t;\n  font-weight: 500;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin label {\n  @include t;\n  font-weight: 700;\n  font-size: 12px;\n  line-height: 16px;\n  text-transform: uppercase;\n}\n\n// Кнопки\n\n@mixin button {\n  font-style: normal;\n  font-weight: 500;\n}\n\n@mixin buttonGiant {\n  @include button;\n  font-size: 18px;\n  line-height: 24px;\n}\n\n@mixin buttonLarge {\n  @include button;\n  font-size: 16px;\n  line-height: 20px;\n}\n\n@mixin buttonMedium {\n  @include button;\n  font-size: 14px;\n  line-height: 16px;\n}\n\n@mixin buttonSmall {\n  @include button;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin buttonTiny {\n  @include button;\n  font-size: 10px;\n  line-height: 12px;\n  letter-spacing: 1px;\n}\n\n// Прочее\n\n@mixin round($r) {\n  width: $r;\n  height: $r;\n  min-width: $r;\n  min-height: $r;\n  border-radius: 50%;\n}\n"],"sourceRoot":""}]);
