w {\n  display: flex;\n  min-height: 8px;\n  align-items: center;\n}\n\n.history__details-row:last-of-type {\n  padding-bottom: 32px;\n}\n\n.history__details-row:last-of-type.history__details-row--ext {\n  padding-bottom: 40px;\n}\n\n.request__history-element:last-of-type .history__details-row:last-of-type {\n  padding-bottom: 0;\n}\n\n.history__details-name {\n  @include s1;\n}\n\n.history__details-name--unknown {\n  color: var(--base-500);\n}\n\n.history__details-date {\n  @include p2;\n  color: var(--base-500);\n  margin-left: 16px;\n}\n\n.history__details-info {\n  @include p2;\n  color: var(--base-500);\n}\n\n.history__details-status {\n  @include s2;\n  margin-left: 16px;\n  line-height: 18px;\n  color: var(--base-500);\n}\n\n.history__details-status--1 {\n  color: var(--danger-500);\n}\n\n.history__details-status--2 {\n  color: var(--warning-500);\n}\n\n.history__details-status--3 {\n  color: var(--success-500);\n}\n\n.history__details-comment {\n  margin-top: 8px;\n  padding: 16px;\n  background: var(--accent-100);\n  border-radius: 0 16px 16px 16px;\n  @include p1;\n}\n\n.request__history-attachments {\n  margin-top: 32px;\n}\n.history__info{\n  margin-left: 8px;\n}\n.tooltip-users__wrapper{\n  display: flex;\n  padding-bottom: 16px;\n}\n.tooltip-users__wrapper:last-of-type{\n  padding-bottom: 0;\n}\n.tooltip-users__name{\n  @include p1;\n  color:var(--base-1000)\n}\n.tooltip-users__position{\n  @include p1;\n  color:var(--base-500);\n}\n.tooltip__wrapper{\n  padding: 16px;\n}\n.tooltip-users__info{\n  padding-left: 16px;\n}\n.tooltip-users__other{\n  @include p1;\n  padding-left: 8px;\n  color:var(--base-500)\n}","\n// Текст\n@mixin text($size: 18px, $weight: 400, $color: $black, $fontStyle: normal) {\n  font-size: $size;\n  font-weight: $weight;\n  font-style: $fontStyle;\n  color: $color;\n}\n\n// Неактивное состояние элемента\n@mixin disabled() {\n  cursor: default;\n  pointer-events: none;\n}\n\n// Визуально скрыть\n@mixin hidden {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 1px;\n  height: 1px;\n  clip: rect(1px, 1px, 1px, 1px);\n}\n\n// ------------------\n\n// Заголовки\n\n@mixin h {\n  font-style: normal;\n  font-weight: 700;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n}\n\n@mixin h1 {\n  @include h;\n  font-size: 36px;\n  line-height: 48px;\n}\n\n@mixin h2 {\n  @include h;\n  font-size: 32px;\n  line-height: 40px;\n}\n\n@mixin h3 {\n  @include h;\n  font-size: 30px;\n  line-height: 40px;\n}\n\n@mixin h4 {\n  @include h;\n  font-size: 26px;\n  line-height: 32px;\n}\n\n@mixin h5 {\n  @include h;\n  font-size: 22px;\n  line-height: 32px;\n}\n\n@mixin h6 {\n  @include h;\n  font-size: 18px;\n  line-height: 24px;\n}\n\n// Подзаголовки\n\n@mixin s {\n  font-style: normal;\n  font-weight: 500;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n  line-height: 24px;\n}\n\n@mixin s1 {\n  @include s;\n  font-size: 15px;\n}\n\n@mixin s2 {\n  @include s;\n  font-size: 13px;\n}\n\n// Тексты\n\n@mixin t {\n  font-style: normal;\n  font-weight: 400;\n  letter-spacing: 0.01em;\n  color: var(--base-1000);\n}\n\n@mixin p1 {\n  @include t;\n  font-size: 15px;\n  line-height: 20px;\n}\n\n@mixin p2 {\n  @include t;\n  font-size: 13px;\n  line-height: 18px;\n}\n\n@mixin c1 {\n  @include t;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin c2 {\n  @include t;\n  font-weight: 500;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin label {\n  @include t;\n  font-weight: 700;\n  font-size: 12px;\n  line-height: 16px;\n  text-transform: uppercase;\n}\n\n// Кнопки\n\n@mixin button {\n  font-style: normal;\n  font-weight: 500;\n}\n\n@mixin buttonGiant {\n  @include button;\n  font-size: 18px;\n  line-height: 24px;\n}\n\n@mixin buttonLarge {\n  @include button;\n  font-size: 16px;\n  line-height: 20px;\n}\n\n@mixin buttonMedium {\n  @include button;\n  font-size: 14px;\n  line-height: 16px;\n}\n\n@mixin buttonSmall {\n  @include button;\n  font-size: 12px;\n  line-height: 16px;\n}\n\n@mixin buttonTiny